<template>
	<div class="personal-content-body">
		<div class="content">
			<div class="content-title flex_between">
				<!-- <div class="">审批模版列表</div> -->
				<div class="">
					<div class="table-group-title-btn btn-blue" @click="gotoCreatetemp"><span class="iconfont icon-jia"></span>新建审批模版</div>
				</div>
			</div>
			<div class="applytemp_body">
				<div class="table-nodata" v-if="applyTypelist.length==0">
					<img src="../../images/table_nodata.png"> 没有对应数据
				</div>
				<div class="applytemp_group" v-else v-for="item in applyTypelist">
					<div class="applytemp_group_title">
						<div class="applytemp_group_title_left">
							{{item.title}}<span class="fcolor_gray">（{{item.children.length}}）</span>
						</div>
					</div>
					<div class="applytemp_list">
						<div class="applytemp_item" v-for="item1 in item.children">
							<div class="applytemp_item_row flex">
								<img :src="item1.url" class="applytemp_item_img">
								<div class="flex_column flex_grow">
									<div class="fsize_15 lineheight_1.5">{{item1.name}}</div>
									<div class="fsize_12 fcolor_gray_85 applytemp_item_ellipsis lineheight_2">
										{{item1.description}}
									</div>
									<div class="fsize_12 fcolor_gray_85 lineheight_2">{{item1.update_time}} 更新</div>
								</div>
								<div class="line"></div>
							</div>
							<div class="applytemp_item_row flex flex_grow">
								<div class="fsize_14 flex_column flex_grow">
									<div class="fsize_12 lineheight_2">可见范围</div>
									<div class="fsize_12 fcolor_gray_85">
										<span class="marginr_5 fcolor_gray_85 lineheight_2" v-if="item1.visible==0">全部可见</span>
										<span class="marginr_5 fcolor_gray_85 lineheight_2" v-if="item1.visible!=0">部分可见</span>
										<span class="applytemp_item_link lineheight_2" @click="changeVisible(item1.visible,item1.id)">修改</span></div>
								</div>
								<div class="line"></div>
							</div>
							<div class="applytemp_item_row flex flex_grow">
								<div class="fsize_12 applytemp_item_link marginr_5" @click="gotoApplytempProcess(item1.id,item.type)">审批流程</div>
								<div class="fsize_12 applytemp_item_link marginr_5" @click="changeTempstatus(item1.id,1)" v-if="item1.status==0">启用</div>
								<div class="fsize_12 applytemp_item_link marginr_5" @click="changeTempstatus(item1.id,0)" v-if="item1.status==1">停用</div>
								<div class="fsize_12 applytemp_item_link marginr_5" @click="gotoUpdateTemp(item1.id)">编辑</div>
								<div class="fsize_12 applytemp_item_link marginr_5" @click="changeTempstatus(item1.id,-1)">删除</div>
							</div>
						</div>
					</div>
				</div>
			</div>
			<!--  设置权限弹窗-->
			<el-dialog class="choosePeopleDialog" title="设置可见人员" :visible.sync="visibleDialog" width="800px" center :append-to-body='true' :before-close="visibleClose">
				<div class="dialog_content flex">
					<div class="dialog_content_content">
						<div class="dialog_content_content_title">选择人员</div>
						<div>
							<el-input placeholder="输入关键字进行过滤" v-model="filterText">
								<i slot="prefix" class="el-input__icon el-icon-search"></i>
							</el-input>
						</div>
						<div class="custom_tree_node flex">
							<el-checkbox v-model="visibleCheckedall" @change="changeVisibleCheckedall">全选</el-checkbox>
						</div>
						<el-tree class="dialog_content_content_tree" :data="visibleList" show-checkbox node-key="id" :default-checked-keys="defaultChechedArr" :filter-node-method="filterNode" ref="tree" :props="defaultProps" @check="visivleListChange">
							<span class="custom_tree_node flex" slot-scope="{ node, data }">
              <img :src="node.data.url"  @error="getImgurl" v-if="node.data.url!=undefined">
              {{node.data.name}}
            </span>
						</el-tree>
					</div>
					<div class="dialog_content_content">
						<div class="dialog_content_content_title">已选人员</div>
						<div class="selected_list">
							<div class="selected_item" v-for="people in checkedVisibleStr">
								<img class="selected_item_headimg" :src="people.url" @error="getImgurl">{{people.name}}
								<i class="el-icon-error" @click="deleteCheckedVisivle(people.id)"></i></div>
						</div>
					</div>
				</div>
				<div slot="footer" class="dialog_footer clear">
					<button class="dialog-btn-submit btn-blue" @click="commitVisible">确定</button>
					<button class="dialog-btn-submit btn-blue-border " @click="visibleClose">取消</button>
				</div>
			</el-dialog>
		</div>
	</div>
</template>
<script>
	//审批流程状态
	import { dialogConfirm, compareJson, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getApplyTempNew, getUserCompany, applyTempblockNew, visibleRangeNew } from '../../config/api'
	export default {
		data() {
			return {
				applyTypelist: [],
				routeList: [],
				//权限弹窗///////////////////////////////
				visibleDialog: false,
				template_id: '',
				filterText: '', //人员筛选
				visibleList: [], //权限列表
				defaultProps: {
					children: 'children',
					label: 'name'
				},
				visibleCheckedall: false,
				checkedVisibleStr: [],
				defaultChechedArr: [], //所有用户数组id
				allVisibleStr: [],
				// defaultExpandedArr:[],
			}
		},
		created() {
			if(this.routeList.length != 0) {} else {
				this.routeList = this.$route.meta.routeList;
			}
			this.initdata();
		},
		components: {},
		computed: {},
		mounted() {
			// window.onbeforeunload = function(event) {
			//   return confirm("确定退出吗");
			// }
		},
		watch: {
			filterText(val) {
				this.$refs.tree.filter(val);
			}
		},
		methods: {
			initdata() {
				//获取审批类型
				getApplyTempNew({}).then(res => {
					if(res.data.msgNo == 1) {
						this.applyTypelist = res.data.payload;
					}
				})
			},
			getImgurl(e) {
				e.currentTarget.src = '../../static/images/header_userimg.png';
				e.onerror = null;
			},
			//修改模版状态
			changeTempstatus(template_id, status) {
				let message = '';
				let message1 = '';
				if(status == -1) {
					message = '确认删除吗？';
					message1 = '删除成功';
				} else if(status == 0) {
					message = '确认停用吗？';
					message1 = '停用成功';
				} else if(status == 1) {
					message = '确认启用吗？'
					message1 = '启用成功'
				}
				dialogConfirmHasbtn(message).then(res => {
					applyTempblockNew({
						tid: template_id,
						status: status
					}).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm('success', message1);
						} else {
							dialogConfirm('fail', res.data.message);
						}
						this.initdata();
					})
				}).catch(() => {})
			},
			//前往流程配置
			gotoApplytempProcess(template_id, type) {
				this.$router.push({
					name: 'applytempProcessNew',
					query: {
						template_id: template_id,
						type: type
					}
				})
			},
			//前往编辑模版页
			gotoUpdateTemp(id) {
				this.$router.push({
					name: 'createApplyTemp',
					query: {
						id: id
					}
				})
			},
			////////////////////////////可见配置弹窗//////////////////////
			//显示修改可见弹窗
			changeVisible(visible, template_id) {
				this.visibleDialog = true;
				this.template_id = template_id;
				this.filterText = '';
				this.getUserRule(visible);
			},
			//获取用户权限信息
			getUserRule(visible) {
				getUserCompany({
					is_all: 1
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.visibleList = res.data.payload;
						let vl = res.data.payload;
						let allVisibleStr = [];
						vl.forEach(item => {
							if(item.children == undefined || item.children.length == 0) {
								item.disabled = true
							}
							item.id = -item.id;
							if(item.children != undefined) {
								item.children.forEach(item1 => {
									allVisibleStr.push(item1.id);
								})
							}
						})
						this.allVisibleStr = allVisibleStr;
						this.visibleList = vl;
						if(visible != 0) {
							visible = visible.toString().split(',');
							if(visible.length == allVisibleStr.length) {
								this.visibleCheckedall = true;
							} else {
								this.visibleCheckedall = false;
							}
							this.setCheckedKeyStr(visible);
							this.$refs.tree.setCheckedKeys(visible);
						} else {
							this.visibleCheckedall = false;
							this.checkedVisibleStr = [];
							this.$refs.tree.setCheckedKeys([]);
						}
					} else {
						dialogConfirm('fail', res.data.message);
					}
				})
			},
			//人员筛选
			filterNode(value, data) {
				if(!value) return true;
				return data.name.toString().indexOf(value) !== -1;
			},
			//全选/全不选
			changeVisibleCheckedall(e) {
				if(e) {
					this.$refs.tree.setCheckedKeys(this.allVisibleStr);
					this.setCheckedKeyStr(this.allVisibleStr);
				} else {
					this.$refs.tree.setCheckedKeys([]);
					this.setCheckedKeyStr([]);
				}
			},
			//选中人员变化
			visivleListChange(item1, item2) {
				let newArr = [];
				item2.checkedNodes.forEach(element => {
					if(element.children == undefined) {
						let newObj = {};
						newObj.id = element.id;
						newObj.url = element.url;
						newObj.name = element.name
						newArr.push(newObj);
					}
				});
				this.checkedVisibleStr = newArr;
				if(this.checkedVisibleStr.length == this.allVisibleStr.length) {
					this.visibleCheckedall = true;
				} else {
					this.visibleCheckedall = false;
				}
			},
			//获取已选中人员
			setCheckedKeyStr(visible) {
				let vl = this.visibleList;
				let cvs = [];
				vl.forEach(item => {
					if(item.children != undefined) {
						cvs = cvs.concat(item.children.filter(item1 => {
							return visible.indexOf(item1.id.toString()) != -1 || visible.indexOf(item1.id) != -1
						}))
					}
				})
				this.checkedVisibleStr = cvs;
			},
			compare(property) {
				return function(a, b) {
					var value1 = a[property];
					var value2 = b[property];
					return value1 - value2;
				}
			},
			deleteCheckedVisivle(id) {
				this.checkedVisibleStr.forEach((element, index) => {
					if(id == element.id) {
						this.checkedVisibleStr.splice(index, 1);
					}
				});
				let newArr = [];
				this.checkedVisibleStr.forEach((element, index) => {
					newArr.push(element.id);
				});
				if(this.checkedVisibleStr.length == this.allVisibleStr.length) {
					this.visibleCheckedall = true;
				} else {
					this.visibleCheckedall = false;
				}
				this.$refs.tree.setCheckedKeys(newArr);
			},
			//设置权限弹窗确定按钮
			commitVisible() {
				let str = '';
				if(this.checkedVisibleStr.length == 0 || this.checkedVisibleStr.length == this.allVisibleStr.length) {
					str = 0;
				} else {
					str = [];
					this.checkedVisibleStr.forEach(item => {
						str.push(item.id);
					})
				}
				str = str.toString();
				visibleRangeNew({
					tid: this.template_id,
					visible: str
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '修改成功！');
						this.visibleDialog = false;
						this.initdata();
					} else {
						dialogConfirm('info', res.data.message);
					}
				})

			},
			gotoCreatetemp() {
				this.$router.push({
					name: 'createApplyTemp'
				})
			},
			//关闭弹窗之前调用
			visibleClose() {
				this.template_id = '';
				this.checkedVisibleStr = [];
				this.$refs.tree.setCheckedKeys([]);
				this.visibleCheckedall = false;
				this.visibleDialog = false;
			},
			getImgurl(e) {
				e.currentTarget.src = '../../static/images/header_userimg.png';
				e.onerror = null;
			}
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/apply';
	.table-group-title {
		padding: 0px;
	}
	
	.applytemp_body {
		width: 100%;
		.applytemp_group_title {
			width: 100%;
			font-size: 16px;
			height: 40px;
			display: flex;
			align-items: center;
			color: #323334;
			padding: 0 9px;
			text-align: left;
			background: #f8f8f8;
		}
		.applytemp_list {
			width: 100%;
			margin-bottom: 10px;
			.applytemp_item {
				display: flex;
				align-items: center;
				padding: 14px 0px;
				min-width: 0;
				border-bottom: 1px solid #e0e0e0;
				&:last-child {
					border: none;
				}
			}
			.applytemp_item_row {
				padding: 0px 14px;
				width: 33%;
				flex-shrink: 0;
				position: relative;
				align-items: center;
				.line {
					height: 60px;
					width: 1px;
					background: #e0e0e0;
					flex-shrink: 0;
				}
				.flex_column {
					min-width: 0;
					overflow: hidden;
				}
			}
			.applytemp_item_img {
				width: 50px;
				height: 50px;
				border-radius: 50%;
				flex-shrink: 0;
				margin-right: 12px;
			}
			.applytemp_item_ellipsis {
				min-width: 0;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
				padding-right: 10px;
			}
			.lineheight_2 {
				line-height: 1.5em;
			}
			.applytemp_item_link {
				color: #53b8ff !important;
				cursor: pointer;
			}
		}
	}
</style>


